@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;600;700&display=swap');
body {
  margin: 0; 
  font-family: 'Open Sans', sans-serif;
  font-weight: 400;
}
a:hover {text-decoration: none;}
header {
  background: blue; 
  text-align: right;
  color: #fff;
  padding: 10px 30px;
  display: flex;
  justify-content: flex-end;
}
.topLinks a {
  color: #fff;
  font-size: 26px;
  margin-left: 40px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  position: relative;
}

small.Recount {
  position: absolute;
  top: -4px;
  right: 5px;
  font-size: 12px;
  width: 65px;
  height: 65px;
  background: orange;
  line-height: 65px;
  text-align: center;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.8s ease;
}
small.Recount.show { 
  width: 25px;
  height: 25px; 
  line-height: 25px; 
  opacity: 1;
}
.topLinks a span{
    display: block;
    font-size: 16px;
    font-weight: 600;
}
.letfMenu {
  background:  blue;
  padding: 50px 50px 15px;
  margin-top: 30px;
}
.letfMenu a {
  color: #333;
  font-size: 16px; 
  display: block;
  text-align: center;
  text-decoration: none;
  background:#fff;
  padding: 15px;
  margin-bottom: 30px;
}
.contentArea {
	min-height: 485px;
	text-align: center; 
}
.msgBox {
  max-width: 500px;
  width: 100%;
  padding: 25px;
  background-color: #fff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.10);
  margin: 70px auto 0;
  text-align: center;
  transition: all 0.3s ease;
}
.img_avatar {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 2px solid #ddd;
  padding: 3px;
}
.msgBox h1 {
  color: #333;
  font-size: 20px; 
  display: block;
  text-align: center; 
  padding: 15px;
  margin-bottom: 30px;
  font-weight: 600;
}
.chatBox {
	position: absolute;
  bottom: 50px;
  color: blue;
	right: 50px;
	border: 2px solid blue;
	border-radius: 50%;
	width: 100px;
	height: 100px;
	text-align: center;
	line-height: ;
	font-size: 17px;
	font-weight: 600;
}
.chatBox .fa {
	display: block;
	font-size: 26px;
	margin-top: 18px;
}

.guideBox {
  background-color: rgba(0, 0, 0, 0.6);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
  transition: all 0.3s ease;
}
.ShowGuide {
  opacity: 1;
  z-index: 1;
}
.Zindex {
  z-index: 2;
  position: relative;
}
.Zindex2 {
  z-index: 2;
  color: #fff;
  border-color: #fff;
}
.screen1.ShowGuide .msgBox {
  position: absolute;
  top: 100px;
  margin: 0;
  right: 282px; 
}
.screen1.ShowGuide .msgBox:after {
  position: absolute;
  width: 0;
  height: 0;
  border-left: 15px solid transparent;
  border-right: 15px solid transparent;
  content: "";
  border-bottom: 15px solid #fff;
  top: -15px;
  right: 25px;
  transition: all 0.3s ease;
}
.screen2.ShowGuide .msgBox {
  position: absolute;
  bottom: 195px;
  margin: 0;
  left: 550px; 
}
.screen2.ShowGuide .msgBox:after {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  content: "";
  border-right: 15px solid #fff;
  top: 20px;
  left: -15px;
  transition: all 0.3s ease;
}

.screen3.ShowGuide .msgBox {
  position: absolute;
  bottom: 365px;
  margin: 0;
  left: 550px; 
}
.screen3.ShowGuide .msgBox:after {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  content: "";
  border-right: 15px solid #fff;
  top: 20px;
  left: -15px;
  transition: all 0.3s ease;
}
.screen4.ShowGuide .msgBox {
  position: absolute;
  bottom: 68px;
  margin: 0;
  right: 200px;
}
.screen4.ShowGuide .msgBox:after {
  position: absolute;
  width: 0;
  height: 0;
  border-top: 15px solid transparent;
  border-bottom: 15px solid transparent;
  content: "";
  border-left: 15px solid #fff;
  bottom: 20px;
  right: -15px;
  transition: all 0.3s ease;
}
.img_reward {
  position: absolute;
  top: -30px;
  left: -40px;
  width: 0;
  opacity: 0;
  transition: all 0.7s ease;
}
.Zindex .img_reward {
  opacity: 1;
  width: 150px;
}